		submit:function(){
			
			let data={
				    inquiryNumber: _inquiryNumber,      //询价单号
					basicsPrice: _this.finalPrice,			//基础价格
					computationRule: 1    ,	//计算规则
					isIcash: _this.carriageIndex    ,				//是否含运费
					isPlusDuty: _this.taxedIndex    ,			//是否含税
					// freight: 0  ,				//运费
					freight: _this.carriagePrice   ,				//运费
